function sfsi_processfurther(ref) {
	var feed_id =
		'M3g4YWNVWkkvam9WYmNqWkI4YThWVEliVllEZ0tqTzFJa2UyTERHU0xFN2ZzT2llWWl5NWFlaWYxZmpMa05WVE5BOGlnS05OeWNOSlBzK3JTVTJQSUVxZmc2RXFEbzh5aWdjeDh1RkVHMURLM2dySDFkWHRhUWdqTnNWaDBFait8N3V2WFI5bHVybUFrYlJKQnJnY3NFZjV0SjBaNUR1ZW1EQm9mQ3MxUzVzQT0=';
	var feedtype = 8;
	var email = jQuery(ref).find('input[name="email"]').val();
	var filter =
		/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	if (email != 'Enter your email' && filter.test(email)) {
		if (feedtype == '8') {
			var url =
				'https://api.follow.it/subscription-form/' +
				feed_id +
				'/' +
				feedtype;
			window.open(
				url,
				'popupwindow',
				'scrollbars=yes,width=1080,height=760'
			);
			return true;
		}
	} else {
		alert('Please enter email address');
		jQuery(ref).find('input[name="email"]').focus();
		return false;
	}
}

